/**
 * TSL(Tab-Scroll-Load)
 * tabajax
 */

//lib
var IScroll = require('./mods/iscroll-lite.js'); //iScroll
var Sticky = require('./mods/sticky.js'); //fixed
var Loading = require('./mods/loading.js');


function TSL(options) {

	//
	this.opts = $.extend(true, {
		isDisableAutoLoad: false,	//
		isDisableScroll: false,		// truethis.opts.scroll
		defaultTabIndex: 0,			//1tab
		startPage: 1,				//
		iScroll: '.J_iScroll',		//iScrollclassnulliscroll
		isTabSticky: true,			//tab
		className: {
			tabWrap: 'J_tabWrap',			//tab
			tabActive: 'tab-active',		//tab
			tabTag: 'li',					//tabli
        	contWrap: 'J_contWrap',			//cont
			contActive: 'cont-active', 		//tab
			cont: 'cont',					//conthtml
        	items: '__items__'				//cont__loading__
		},
		load: function() {
			//
		},				
		render: function (data) {
			//dataajax
			//this.$itemtabcontdata
		},
		tabClick: function($obj) {
			//tab$objjq
		},				
		scroll: function(scrollTop) {
			//scroll scrollToptop
		}
	}, options);

	this.WINDOW_HEIGHT = $(window).height();

	//jquery obj
	this.$tabWrap = $('.' + this.opts.className.tabWrap);	//tab
	this.$contWrap = $('.' + this.opts.className.contWrap);	//conts
	this.$cont = null; 		//cont
	this.$items = null;		//tancont<div class="__items__"></div>
	
	//tabthis.model1
	this.arr_tab = [];

	//0
	this.curTabIndex = this.opts.defaultTabIndex || 0;

	//tabthis.model
	this.oCurTab = {};

	//tab
	this.isHasTab = this.$tabWrap.length ? true : false;

	this.y = 0; //window.scrollTo(x, y)

	this.loader = new Loading(this.opts.loading);

	this.timer = null; 			//load

	this.init();
}

TSL.prototype = {
	init: function() { //
		//loadingloading
		this.loader.init();
		this.setSticky();
		this.setIScroll();
		this.bindClick();
		this.setDOM();
		this.start();
		this.bindEvent();
	},
	/**
	 * TSL
	 * tabisEnd
	 */
	start: function() {
		if (this.arr_tab[this.curTabIndex].isEnd) {
			return false
		}
		var me = this;
		me.getContainer();
		me.loader.show();
		clearTimeout(me.timer);
		me.timer = setTimeout(function() {
			me.opts.load.call(me);
		}, 500);
	},
	render: function (data) { //ajaxdataajax
		var me = this;
		this.$items = this.$cont.eq(this.curTabIndex).find('.' + this.opts.className.items);
		this.opts.render.call(this, data);
		this.oCurTab.page++;
		this.oCurTab.isRender = true;
		this.loader.inform('-  -');

		if (!this.opts.isDisableAutoLoad && !this.isOut()) { // & 
			me.start();
		} else {
			this.setMinHeight();
		}
	},
	fail: function (msg) {
		this.oCurTab.isEnd = true;
		this.loader.inform(msg);
		this.setMinHeight();
	},
	bindClick: function() {
		if (!this.isHasTab) {
			return false;
		}
		var me = this;

		this.$tabWrap.on('click', me.opts.className.tabTag, function() {
			
			if ($(this).hasClass(me.opts.className.tabActive)) {
				return false
			};

			var index = $(this).index();
			me.curTabIndex = index;
			me.oCurTab = me.arr_tab[index];

			$(this).addClass(me.opts.className.tabActive)
				   .siblings(me.opts.className.tabTag).removeClass(me.opts.className.tabActive);

			me.$cont.eq(index).addClass(me.opts.className.contActive).show()
			  .siblings('.' + me.opts.className.cont).removeClass(me.opts.className.contActive).hide();

			if (me.opts.iScroll) {
				setTimeout(function () {
					window.myScroll.scrollToElement("li:nth-child(" + (index + 1) + ")", 200, true);
				}, 200);
			}

			//cont

			/**
			 * bugtab
			 * tab
			 * -----------------------------------------
			 * tab
			 *    tab
			 *    scroll
			 * tab
			 *    tab,scrollTo
			 *    tabscrollscrollTop
			 * 
			 *    a.tab1tab
			 *	  b.tabtabtab1
			*	  c.tab1tabtab
			*	  d.tab1tab1ba
			*/
			var cur_scroll = me.oCurTab.scroll;
			if (me.opts.isTabSticky && me.sticky.isFixed) { //sticky && tab
				if (!me.oCurTab.isRender) { //
					window.scrollTo(0, me.y);
					me.start();
				} else { //
					//tabtabtab
					cur_scroll = cur_scroll < me.sticky.arr_tops[0] ? me.y : cur_scroll;
					window.scrollTo(0, cur_scroll);
				}
			} else { //tabtab
				me.oCurTab.scroll = $(window).scrollTop(); //
				if (!me.oCurTab.isRender) {
					me.start();
				}
			}

			//click
			me.opts.tabClick.call(me, $(this));
		});
	},
	bindEvent: function() {
		var me = this;
		window.addEventListener('scroll', function () {
			if (me.opts.isDisableScroll || me.loader.isLoading()) {
				return false
			}
			var scrollTop = $(window).scrollTop();
			var docHeight = $(document.body).height();
			if (scrollTop >= docHeight - me.WINDOW_HEIGHT) { //
				me.start();
			}

			if (me.isHasTab) {
				me.oCurTab.scroll = scrollTop; //tab
			}

			//
			me.opts.scroll.call(me, scrollTop);
		});
	},
	getContainer: function() {
		var $container = this.$cont.eq(this.curTabIndex);
		this.loader.container = $container;
	},
	isOut: function() {
		//+   
		return (this.loader.container.height() + this.loader.container.offset().top > this.WINDOW_HEIGHT) ? true : false
	},
	/**
	 * 
	 */
	setMinHeight: function () {
		if (this.isSetMinHeight) {
			return false
		}
		if (this.isOut() && this.isHasTab) { //tabtab
			this.$contWrap.css('min-height', this.WINDOW_HEIGHT);
			this.isSetMinHeight = true;
		}
	},
	/**
	 * tab
	 */
	setSticky: function () {
		if (this.isHasTab && this.opts.isTabSticky) { //tab
			this.sticky = this.$tabWrap.sticky({
				top: -1
			});
			//
			this.y = this.sticky.arr_tops[0] + 2; //+2
		}
	},
	/**
	 * iscroll
	 */
	setIScroll: function () {
		if (this.isHasTab && this.opts.iScroll) { //tabiScroll
			window.myScroll = new IScroll(this.opts.iScroll, {

			});
			console.log(myScroll);
		}
	},
	/**
	 * tabcont
	 * tabarr_tabmodel
	 */
	setDOM: function () {
		var me = this;
		var sCont = '';
		//oCurTab
		var model = {
			page: this.opts.startPage,   //1
			isRender: false,			 //
			isEnd: false, 		//load
			scroll: 0			//scrollTop
		};
		if (this.isHasTab) {
			//dom
			this.$tabWrap.find(me.opts.className.tabTag).each(function (index, el) {
				//tab
				me.arr_tab.push($.extend(true, {}, model));
				sCont += '<div class="'+ me.opts.className.cont +' '+ (index == me.curTabIndex ? me.opts.className.contActive : '') +'">\
								<div class="'+ me.opts.className.items +'"></div>\
							</div>';
			});
		} else {
			this.arr_tab.push($.extend(true, {}, model));
			sCont += '<div class="'+ me.opts.className.cont +' '+ me.opts.className.contActive +'">\
								<div class="'+ me.opts.className.items +'"></div>\
						</div>';
		}
		//licont
		this.$contWrap.html(sCont);
		this.$cont = this.$contWrap.find('.' + this.opts.className.cont);

		//cont
		this.$items = this.$cont.eq(this.curTabIndex).find('.' + this.opts.className.items);
		
		//arr_tabtab
		me.oCurTab = me.arr_tab[me.curTabIndex];
	},
	/**
	 * 
	 */
	reload: function() {
		var me = this;
		me.loader.loading.html('<input value="" class="reloadBtn" type="button">');
		me.$cont.on('click', '.reloadBtn', function() {
			me.oCurTab.isEnd = false;
			me.start();
		});
	}
};

window.TSL = TSL;

if (typeof module != 'undefined' && module.exports) {
	module.exports = TSL;
}





