			if ($(document.body).height() !== me.BODY_HEIGHT) {
				me.getOffsetTop();
				me.BODY_HEIGHT = $(document.body).height();
			}

			/**
			 * _st = st + me.opts.top 
			 * me.curIndex
			 * me.curIndex
			 */
